import React from 'react';
import s from './MusicFunctional.module.css';

import { useState, useEffect } from 'react';
import { searchYouTube } from '../../api/api';

const Music = () => {
  const [query, setQuery] = useState('European history');
  const [list, setList] = useState(null);
  const search = (e) => {
    e.preventDefault();
    searchYouTube(query).then(setList);
  };
  return (
    <div className={s.ap}>
      <form onSubmit={search}>
        <input autoFocus value={query} onChange={e => setQuery(e.target.value)} />
        <button>Search YouTube</button>
      </form>
      {list &&
        (list.length === 0
          ? <p>No results</p>
          : (
            <ul className={s.items}>
              {list.map(item => (
                <li className={s.item} key={item.id}>
                  <div>
                    <b><a href={item.link}>{item.title}</a></b>
                    <img alt ="ava" src={item.author.bestAvatar.url}/>
                  </div>
                  <ul className={s.meta}>
                    <li>By: <a href={item.author.ref}>{item.author.name}</a></li>
                    <li>Views: {item.views}</li>
                    <li>Duration: {item.duration}</li>
                    <li>Uploaded: {item.uploadedAt}</li>
                  </ul>
                  <img alt="title_img" src={item.bestThumbnail.url} />
                </li>
              ))}
            </ul>
          )
        )
      }
    </div>
  );
}

export default Music;